.tu-input {
  position: relative;
  display: inline-block;
  width: 100%; }
  .tu-input.is-disabled {
    cursor: not-allowed; }
    .tu-input.is-disabled .tu-input__inner {
      cursor: not-allowed; }
      .tu-input.is-disabled .tu-input__inner::-moz-placeholder {
        color: #c8d0e7; }
      .tu-input.is-disabled .tu-input__inner::placeholder {
        color: #c8d0e7; }
    .tu-input.is-disabled::after {
      content: '';
      display: block;
      width: 100%;
      height: 100%;
      position: absolute;
      top: 0;
      right: 0;
      cursor: not-allowed; }
  .tu-input__inner {
    -webkit-appearance: none;
    background-color: transparent;
    background-image: none;
    border-radius: 4px;
    border: none;
    box-sizing: border-box;
    display: block;
    padding: 0 12px;
    font-size: 14px;
    line-height: 32px;
    height: 32px;
    outline: none;
    transition: border-color 0.2s cubic-bezier(0.645, 0.045, 0.355, 1);
    width: 100%;
    color: #9baacf;
    box-shadow: inset 2px 2px 5px #c8d0e7, inset -2px -2px 5px #ffffff;
    transition: 0.3s ease; }
    .tu-input__inner::-moz-placeholder {
      color: #bec8e4; }
    .tu-input__inner::placeholder {
      color: #bec8e4; }
    .tu-input__inner::-ms-reveal {
      display: none; }
    .tu-input__inner:focus {
      outline: none;
      box-shadow: 3px 3px 6px #c8d0e7, -2px -2px 5px #ffffff; }
  .tu-input__prefix {
    position: absolute; }
  .tu-input__suffix {
    position: absolute;
    height: 100%;
    right: 8px;
    top: 0;
    text-align: center;
    color: #bec8e4;
    transition: 0.3s ease;
    pointer-events: none;
    font-size: 14px; }
    .tu-input__suffix-inner {
      display: inline-flex;
      align-items: center;
      pointer-events: all;
      height: 100%; }
    .tu-input__icon {
      cursor: pointer;
      transition: 0.3s ease; }
      .tu-input__icon:hover {
        color: #9baacf; }
  .tu-input--mini .tu-input__inner {
    line-height: 22px;
    height: 22px;
    font-size: 10px;
    padding: 0 12px; }
  .tu-input--mini .tu-input__suffix {
    font-size: 10px; }
  .tu-input--small .tu-input__inner {
    line-height: 28px;
    height: 28px;
    font-size: 12px;
    padding: 0 12px; }
  .tu-input--small .tu-input__suffix {
    font-size: 12px; }
  .tu-input--large .tu-input__inner {
    line-height: 40px;
    height: 40px;
    font-size: 14px;
    padding: 0 12px; }
  .tu-input--large .tu-input__suffix {
    font-size: 14px; }
